<template>
    <div>
        <!-- 카카오 로그인 버튼 -->
        <a id="custom-login-btn" v-if="!isLoggedIn" @click="kakaoLogin">
            <img
                src="https://k.kakaocdn.net/14/dn/btroDszwNrM/I6efHub1SN5KCJqLm1Ovx1/o.jpg"
                width="222"
                alt="카카오 로그인 버튼"
            />
        </a>
        <!-- 카카오 로그아웃 버튼 -->
        <button type="button" v-if="isLoggedIn" @click="kakaoLogout">카카오 로그아웃</button>
    </div>
</template>
<script>
export default {
    components: {},
    data() {
        return {
            code: '' /* 카카오 인증 코드를 저장할 변수 */,
            isLoggedIn: false /* 초기 로그인 상태: 로그아웃 상태 */
        };
    },
    mounted() {
        if (!window.Kakao.isInitialized()) {
            window.Kakao.init('3026b75923ebe4d6d23187fa55dcdfa5');
        }
    },
    methods: {
        /* 카카오 로그인 창 띄우고 사용자 인증 처리 */
        kakaoLogin() {
            window.Kakao.Auth.login({
                scope: 'profile_nickname, profile_image' /* 프로필 이미지, 닉네임 요청 */,
                success: this.getKakaoAccount /* 로그인 성공시 호출할 메서드 */
            });
        },
        /* 로그인 성공 후 사용자 정보를 가져옴  */
        getKakaoAccount() {
            window.Kakao.API.request({
                url: '/v2/user/me' /* 사용자 정보 요청 url */,
                success: (res) => {
                    const kakao_account = res.kakao_account; // 사용자 계정 정보
                    const profile = kakao_account.profile_image; // 카카오 프로필
                    const nickname = kakao_account.profile_nickname; // 카카오 닉네임

                    // 디버깅용 콘솔 출력
                    console.log('profile', profile);
                    console.log('nickname', nickname);

                    /* 로그인 처리 */
                    console.log(kakao_account);

                    this.isLoggedIn = true;
                    alert('로그인 성공!');
                },
                fail: (error) => {
                    /* 사용자 정보 요청 처리 실패시 */
                    alert('로그인에 실패했습니다.');
                    console.log(error);
                }
            });
        },
        kakaoLogout() {
            /* 카카오 로그아웃 처리 및 상태 초기화 */
            if (!window.Kakao.Auth.getAccessToken()) {
                /* 엑세스 토큰이 없는경우 (이미 로그아웃 상태) */
                console.log('로그인 되지 않았습니다');
                return;
            }
            /* 로그아웃 처리 */
            window.Kakao.Auth.logout((response) => {
                /* 로그아웃 */
                console.log('access token:', window.Kakao.Auth.getAccessToken());
                /*  null이 출력되어야함 */
                console.log('log out', response); /* 로그아웃 응답출력 */
                this.isLoggedIn = false; /*  로그아웃 시 상태를 false로 */
            });
        }
    }
};
</script>
