<template>
    <div>
        <!-- 네이버 로그인 -->
        <div id="naverIdLogin" v-if="!accessToken"></div>
        <button type="button" @click="logout" v-if="accessToken">로그아웃</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            naverLogin: null,
            accessToken: null
        };
    },
    mounted() {
        /* 컴포넌트가 화면에 렌더링된 후 실행되는 라이프사이클 훅 */
        this.initializeNaverLoginButton(); /* 네이버 로그인 버튼 초기화 */
    },
    methods: {
        initializeNaverLoginButton() {
            try {
                this.naverLogin = new window.naver.LoginWithNaverId({
                    clientId: '2k09FENR4C97BE3J91Bg' /* 클라이언트아이디 */,
                    callbackUrl: 'http://localhost:8080/naverlogin',
                    isPopup: false,
                    loginButton: { color: 'green', type: 3, height: 60 }
                });
                /* 네이버 로그인 sdk 초기화 */
                this.naverLogin.init();

                /* 초기화 후 로그인 상태 확인 */
                this.naverLogin.getLoginStatus((status) => {
                    if (status) {
                        /* 로그인 상태인 경우 */
                        this.checkLoginStatus();
                    } else {
                        console.log('로그인되지 않은 상태입니다.');
                    }
                });
            } catch (error) {
                /* 초기화 중 오류발생시 에러 출력  */
                console.error('네이버 로그인 초기화 중 오류발생:', error);
            }
        },
        /* 로그인 상태를 확인하고 사용자 정보 가져오기 */
        checkLoginStatus() {
            if (!this.naverLogin) {
                console.error('네이버 로그인 객체가 초기화되지 않았습니다.');
                return;
            }
            /* 로그인 상태 확인 */
            this.naverLogin.getLoginStatus((status) => {
                if (status) {
                    const email = this.naverLogin.user.getEmail();
                    if (!email) {
                        alert('이메일은 필수정보입니다. 정보제공을 동의해주세요.');
                        this.naverLogin.reprompt(); /* 동의 팝업 다시 표시 */
                        return;
                    }
                    /* 엑세스 토큰 저장 */
                    this.accessToken = this.naverLogin.accessToken.accessToken;
                    console.log('로그인성공!', email);
                } else {
                    /* 로그인 상태가 아니거나 콜백 처리 실패 */
                    console.log('콜백 처리에 실패하였습니다.');
                }
            });
        },
        /* 로그아웃 */
        logout() {
            if (!this.accessToken) {
                console.log('로그인 상태가 아닙니다.');
                return;
            }

            /* 로그아웃인 경우 네이버 로그인 버튼 디자인 */
            const logoutUrl = `https://nid.naver.com/oauth2.0/token?grant_type=delete&client_id=2k09FENR4C97BE3J91Bg&client_secret=5uG9hzWgoI&access_token=${this.accessToken}&service_provider=NAVER`;

            /* iframe 생성하여 url호출 */
            const iframe = document.createElement('iframe');
            iframe.style.display = 'none';
            iframe.src = logoutUrl;
            document.body.appendChild(iframe);

            /* 엑세스 토큰 초기화 */
            this.accessToken = null;

            /* DOM 업데이트 후 로그인 버튼 초기화 */
            this.$nextTick(() => {
                this.initializeNaverLoginButton();
            });

            console.log('로그아웃 요청이 실행되었습니다.');
        }
    }
};
</script>
